                                        name: 'app',
                                        cnName: '我的待办1',
                                        to: '/app',
                                    }, {
                                        name: 'home21',
                                        cnName: '审批历史1',
                                        to: '/',
                                    }
                                ]
                            }
                        ]
                    }, {
                        name: 'home2',
                        cnName: '首页2',
                        to: '/home2',
                        icon: 'el-icon-location',
                        hasChild: false,
                        children: []
                    }, {
                        name: '服务管理',
                        cnName: '服务管理',
                        to: '#2',
                        icon: 'el-icon-location',
                        hasChild: true,
                        children: [
                            {
                                name: 'home3',
                                cnName: '服务列表',
                                to: '/home3',
                                hasChild: false,
                            },
