                                name: 'serverAccess',
                                cnName: '服务接入',
                                hasChild: false,
                                to: '/serverAccess'
                            },
                            {
                                name: 'applyList',
                                cnName: '服务注册',
                                hasChild: false,
                                to: '/applyList'
                            },
                        ]
                    }, {
                        name: '系统管理',
                        cnName: '系统管理',
                        icon: 'el-icon-location',
                        to: '#3',
                        hasChild: true,
                        children: [
