                                width: 100%;
                                padding: 5px;
                                cursor: pointer;
                                display: inline-block;
                                height: 31px;
                            }

                            .operate_node {
                                position: absolute;
                                top: 0;
                                right: 0;
                                cursor: pointer;
                            }

                            .detail_url {
                                position: absolute;
