<template>
    <div>
        <Row>
            <Col span="8">
            <cw-collapse :info="info" :contents="contents"></cw-collapse>
            <cw-collapse2 :info="info2" :columns="columns" :datas="datas"></cw-collapse2>
            </Col>
        </Row>
    </div>
</template>

<script>

    export default {
        name: 'home2',
        data() {
            return {
                info: [{p: '第一批信息'}, {p: '第二批信息'}, {p: '第三批信息'}],
                contents: [
                    {con1: '标准运维信息一', con2: '标准运维信息二', con3: '标准运维信息三'},
                    {con1: '标准运维信息四', con2: '标准运维信息五', con3: '标准运维信息六'},
                    {con1: '标准运维信息七', con2: '标准运维信息八', con3: '标准运维信息九'}
                ],
                info2: [{p: '磁盘分区信息'}, {p: '请求信息'}, {p: '故障信息'}],
                columns: [
                    [
                        {
                            title: '分区名称',
                            key: 'name'
                        },
                        {
                            title: '分区大小',
                            key: 'size'
                        },
                        {
                            title: '剩余空间',
                            key: 'left'
                        },
                        {
                            title: '剩余空间比',
                            key: 'percentage'
                        }
                    ],
                    [
                        {
                            title: '请求地址',
                            key: 'place'
                        },
                        {
                            title: '请求次数',
                            key: 'sum'
                        },
                        {
                            title: '成功次数',
                            key: 'success'
                        },
                        {
                            title: '请求结果',
                            key: 'result'
                        }],
                    [
                        {
                            title: '故障位置',
                            key: 'place'
                        },
                        {
                            title: '故障时间',
                            key: 'time'
                        },
                        {
                            title: '故障级别',
                            key: 'grade'
                        },
                        {
                            title: '处理人',
                            key: 'people'
                        }]
                ],
                datas:
                    [
                        [{
                            name: 'C:',
                            size: '99.68GB',
                            left: '77.17GB',
                            percentage: '77.14'
                        }],
                        [{
                            place: 'http://www.baidu.com',
                            sum: '10',
                            success: '10',
                            result: 'none'
                        }],
                        [{
                            place: 'pc-abcde',
                            time: '2010-01-12',
                            grade: 'high',
                            people: 'Jason'
                        }]
                    ]
            }
        }
    }
</script>

<style scoped>

</style>



