<template>
    <div>
        <h3 class="margin-bottom10">折叠面板-表格</h3>
        <Collapse v-model="value1" simple>
            <Panel v-for="(p, index) in info" :key="index">
                {{p.p}}
                <div slot="content">
                    <Table border :columns="columns[index]" :data="datas[index]"></Table>
                </div>
            </Panel>
        </Collapse>
    </div>

</template>

<script>
    export default {
        /**
         * 参考 https://www.iviewui.com/components/collapse
         * 面板内容以表格展示
         * @params {objects list} info 面板的名称
         * @params {list} columns 表头,不同列表的表头列表按照顺序存储在columns中
         * @params {list} datas 表格数据，不同列表的数据列表按照顺序存储在datas中
         * */
        name: 'cw-collapse2',
        // props: ['info', 'columns', 'data'],
        props: {
            info: {
                default: [{p: '磁盘分区信息'}, {p: '请求信息'}, {p: '故障信息'}],
            },
            columns: {
                default: [
                    [
                        {
                            title: '分区名称',
                            key: 'name'
                        },
                        {
                            title: '分区大小',
                            key: 'size'
                        },
                        {
                            title: '剩余空间',
                            key: 'left'
                        },
                        {
                            title: '剩余空间比',
                            key: 'percentage'
                        }
                    ],
                    [
                        {
                            title: '请求地址',
                            key: 'place'
                        },
                        {
                            title: '请求次数',
                            key: 'sum'
                        },
                        {
                            title: '成功次数',
                            key: 'success'
                        },
                        {
                            title: '请求结果',
                            key: 'result'
                        }],
                    [
                        {
                            title: '故障位置',
                            key: 'place'
                        },
                        {
                            title: '故障时间',
                            key: 'time'
                        },
                        {
                            title: '故障级别',
                            key: 'grade'
                        },
                        {
                            title: '处理人',
                            key: 'people'
                        }]
                ]
            },
            datas: {
                default: [
                    [{
                        name: 'C:',
                        size: '99.68GB',
                        left: '77.17GB',
                        percentage: '77.14'
                    }],
                    [{
                        place: 'http://www.baidu.com',
                        sum: '10',
                        success: '10',
                        result: 'none'
                    }],
                    [{
                        place: 'pc-abcde',
                        time: '2010-01-12',
                        grade: 'high',
                        people: 'Jason'
                    }]
                ]
            }
        },
        data() {
            return {
                // 动态绑定面板的name
                value1: ''
            }
        }
    }
</script>

<style scoped>

</style>



